/* base */
*, *:before, *:after {
  box-sizing: border-box;
}

// Basic Styles
html {
  line-height: 1.3;
  font-size: ($root-font-size / 16px) * 100%;
}

html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
  min-width: 320px;
}

body {
  background: $color-white;
  font-size: rem(16px);
  font-family: $primary-font;
  color: $primary-color;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
}

//  Typography
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  color: $primary-color;
  font-weight: normal;
  font-family: $font-roboto;
  text-decoration: none;
  margin: 0 0 26px 0;
}

.h1, h1 {
  font-size: rem(50px);
  line-height: 1;
  letter-spacing: -1px;
  font-family: $font-intro;
  margin-bottom: 50px;
}

.h2, h2 {
  font-size: rem(30px);
  line-height: 1.14;
  font-family: $font-intro;
}

.h3, h3 {
  font-size: rem(25px);
  line-height: 1.38;
  &--intro {
    font-family: $font-intro;
  }
}

.h4, h4 {
  font-size: rem(21px);
  line-height: 1.38;
  margin-bottom: 20px;
}

.h5, h5 {
  font-size: rem(18px);
  margin-bottom: 20px;
}

.h6, h6 {
  font-size: rem(16px);
  margin-bottom: 20px;
}

.small, small {
  font-size: rem(12px);
  line-height: 1.2;
}

p {
  line-height: 1.6;
  margin: 0 0 28px 0;
}

em, i, .em, .i {
  font-style: italic;
}

strong, b, .strong, .b {
  font-weight: 700;
}

hr {
  border: solid #dddddd;
  border-width: 1px 0 0;
  clear: both;
  margin: 10px 0 30px;
  height: 0;
}

// Links
a, .link {
  color: $link-color;
  text-decoration: underline;
  outline: none;
  transition: color 0.35s, background 0.35s, border 0.35s;
  &:hover, &:focus, &:active {
    color: $link-color;
    text-decoration: none;
    outline: none;
  }
}

a.link {
  &:hover, &:focus, &:active {
    text-decoration: none;
  }
}

/* text align */
.text {
  &-center, &--center {
    text-align: center !important;
  }
  &-left, &--left {
    text-align: left !important;
  }
  &-right, &--right {
    text-align: right !important;
  }
}

/* loader */
.loader {
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  position: fixed;
  z-index: 10000;
  background-color: $color-white;
  &__block {
    background: $color-blue;
    animation: load1 1s infinite ease-in-out;
    width: 1em;
    height: 4em;
    position: absolute;
    top: 50%;
    left: 50%;
    color: $color-blue;
    text-indent: -9999em;
    font-size: rem(11px);
    animation-delay: -0.16s;
    transform: translate(-50%, -50%);
    &:before, &:after {
      background: $color-blue;
      animation: load1 1s infinite ease-in-out;
      width: 1em;
      height: 4em;
    }
    &:before, &:after {
      position: absolute;
      top: 0;
      content: "";
    }
    &:before {
      left: -1.5em;
      animation-delay: -0.32s;
    }
    &:after {
      left: 1.5em;
    }
  }
  @-webkit-keyframes load1 {
    0%,
    80%,
    100% {
      box-shadow: 0 0;
      height: 4em;
    }
    40% {
      box-shadow: 0 -2em;
      height: 5em;
    }
  }

  @keyframes load1 {
    0%,
    80%,
    100% {
      box-shadow: 0 0;
      height: 4em;
    }
    40% {
      box-shadow: 0 -2em;
      height: 5em;
    }
  }
}

/* img */
.img {
  &-responsive, &--responsive {
    display: block;
    height: auto;
    max-width: 100%;
  }
  &-round, &--round {
    border-radius: 50%;
  }
}

/* section */
.section {
  padding: 50px 0;
  min-width: 320px;
  &__container {
    max-width: 1170px;
    padding: 0 15px;
    margin: 0 auto;
    position: relative;
    min-height: 1px;
    @include clearfix;
    &--fullwidth {
      max-width: inherit;
      width: 100% !important;
      padding: 0;
    }
  }
  &__head {
    margin-bottom: 50px;
    text-align: center;
    .h1, h1 {
      margin-bottom: 0;
    }
  }
}

.container {
  width: 100% !important;
  padding: 0px !important;
  max-width: inherit !important;
  .section {
    min-width: inherit;
  }
}

ul li {
  list-style-type: disc;
}

ol li {
  list-style-type: decimal;
}

/* bg */
.bg {
  &--pink {
    background-color: pink !important;
  }
  &--white {
    background-color: $color-white !important;
  }
  &--black {
    background-color: $color-black !important;
  }
  &--blue {
    background-color: $color-blue !important;
  }
}

/* no padding no margin */
.no {
  &--p {
    padding: 0 !important;
    &-top {
      padding-top: 0 !important;
    }
    &-bottom {
      padding-bottom: 0 !important;
    }
    &-left {
      padding-left: 0 !important;
    }
    &-right {
      padding-right: 0 !important;
    }
  }
  &--m {
    margin: 0 !important;
    &-top {
      margin-top: 0 !important;
    }
    &-bottom {
      margin-bottom: 0 !important;
    }
    &-left {
      margin-left: 0 !important;
    }
    &-right {
      margin-right: 0 !important;
    }
  }
  &--b {
    background: none !important;
    &-image {
      background-image: none !important;
    }
  }
}

/* icons */
.icon {
  display: inline-block;
  // &--sprite
  //   @include sprite($img-name)
}

/* color */
.color {
  &--black {
    color: $color-black !important;
  }
  &--white {
    color: $color-white !important;
  }
  &--blue {
    color: $color-blue !important;
  }
}